<template>
  <section class="grey darken-3 white--text py-10">
    <v-container>
      <base-alert display="app-contact" />
      <v-row>
        <v-col cols="12" md="6">
          <h1>{{ $t('index.contact.heading') }}</h1>
          <base-divider class="mt-1" width="100px" />
          <p class="py-6">
            {{ $t('index.contact.text') }}
          </p>
          <div>
            <div
              v-for="(contact, i) in $store.state.contact.contacts"
              :key="i"
              class="d-flex align-start address-details mt-4"
              style="width:70%"
            >
              <v-avatar size="50" class="mr-3">
                <v-icon size="30" class="primary">{{ contact.icon }}</v-icon>
              </v-avatar>
              <div>
                <h3>{{ contact.title }}</h3>
                <p>
                  {{ contact.detail }}
                </p>
              </div>
            </div>
          </div>
        </v-col>
        <v-col cols="12" md="6">
          <v-form ref="contactForm" v-model="valid" @submit.prevent="save">
            <v-text-field
              v-model="item.name"
              :label="$t('index.contact.nameInput.label')"
              outlined
              rounded
              dark
              :rules="[rules.required]"
              color="primary"
            />
            <v-text-field
              v-model="item.email"
              :label="$t('index.contact.emailInput.label')"
              outlined
              rounded
              dark
              color="primary"
              :rules="[rules.required, rules.isEmail]"
            />
            <v-text-field
              v-model="item.phone"
              :label="$t('index.contact.phoneInput.label')"
              outlined
              dark
              rounded
              color="primary"
              :rules="[rules.required]"
            />
            <v-textarea
              v-model="item.message"
              :label="$t('index.contact.messageInput.label')"
              outlined
              dark
              rounded
              color="primary"
              :rules="[rules.required]"
            />
            <div class="text-center">
              <v-btn
                rounded
                type="submit"
                :loading="$store.state.loading.inquiry"
                large
                color="primary px-8"
                >{{ $t('index.contact.button.text') }}</v-btn
              >
            </div>
          </v-form>
        </v-col>
      </v-row>
    </v-container>
  </section>
</template>

<script>
import { mapActions } from 'vuex'
import validations from '@/mixins/validations'
export default {
  mixins: [validations],
  data() {
    return {
      item: { name: '', email: '', phone: '', message: '' }
    }
  },

  methods: {
    ...mapActions('inquiries', ['addInquiry']),
    async save() {
      this.$refs.contactForm.validate()
      if (this.valid) {
        const payload = {
          inquiry: this.item,
          axios: this.$axios,
          form: this.$refs.contactForm
        }
        await this.addInquiry(payload)
      }
    }
  }
}
</script>

<style scoped>
.address-details {
  border-bottom: 1px solid gray;
}
</style>
